    title: `重要性`,
    content: `
    <p class="kc-text">適當的樹木維護對於促進樹木的健康生長和良好形態非常重要，健康和結構穩定的樹木是環境的寶貴資產。</p>
    <p class="kc-text">樹木倒塌往往是由於不適當的護養所致，例如不當修剪、削頂、損壞根部及其他不恰當的樹木護理方法。樹木倒塌會導致人命傷亡或財物損毀。</p>
    <p class="kc-text">樹木是有生命的，其狀況會隨時間而變化。業主應至少每年檢查一次其物業範圍內的樹木，並在風雨季開始前採取適當防範措施。常規樹木維護工作應由合格的綠化工程承辦商或樹藝師/樹木專家進行。</p>
    <p class="kc-img">	<img src=${require("./../../assets/images/tree/knowloge/risk/risk1.png")}>`
